var symphony = symphony || {};

;(function() {

	symphony.gdosGetServicesByLocation = function() {

		var tempResults = [];
		

		_.forIn(symphony.config.gdos.gdosResults, function(value, key) {
			_.forIn(value.services, function(value, key) {
				tempResults.push(value.category);
			});
		});

		symphony.config.gdos.servicesByLocation = _.sortBy(_.uniqWith(tempResults, _.isEqual), "name");

		symphony.gdosShowServicesByLocation();

	};

})();