var symphony = symphony || {};

;(function() {

	symphony.showOnHover = function() {

		var html = $('html'),
			showOnHoverTrigger = $('.js-showOnHoverParent');

		if(html.hasClass('touchevents')){
			return;
		}
		showOnHoverTrigger.hover(
			function() {
				$(this).find('.js-showOnHover').addClass('isShown');
			}, function() {
				$(this).find('.js-showOnHover').removeClass('isShown');
		});

	};


})();