var symphony = symphony || {};

;(function() {

  symphony.donateHeaderActions = function() {
  	
  		$('.js-donateHeader').on('click', function(e){
  			e.preventDefault();
  			if(!$('.globalHeader').hasClass('isSticky')){
  				$('.globalHeader').addClass('isSticky');
  			};
        if(symphony.config.utilities.expandingHeader.hasClass('isExpanded')){
          symphony.config.utilities.expandingHeader.removeClass('isExpanded');
        } else {
          $('.donateHeader').addClass('isExpanded');
        }
  			
  		});

  };

})();