var symphony = symphony || {};

;(function() {

	symphony.scrollToNextSection = function() {

		$('.js-scrollNextSection').on('click', function(e){

			e.preventDefault();

			var nextDiv = $(this).parent().next('.section');
			var headerHeight = -20;
			$("html, body").animate({ scrollTop: nextDiv.offset().top - headerHeight }, 500);
		});

	};


})();