/* ========================================================================
 * DOM-based Routing
 * Based on http://goo.gl/EUTi53 by Paul Irish
 *
 * Only fires on body classes that match. If a body class contains a dash,
 * replace the dash with an underscore when adding it to the object below.
 *
 * .noConflict()
 * The routing is enclosed within an anonymous function so that you can
 * always reference jQuery with $, even when in .noConflict() mode.
 * ======================================================================== */

var symphony = window.symphony || {};

symphony.config = {
	gdos: {
		postal: "//public.api.gdos.salvationarmy.org/geocode/postal?",
		search: "//public.api.gdos.salvationarmy.org/search?",
		geocode: "//maps.googleapis.com/maps/api/geocode/json?",
		zip: undefined,
		latLong: [],
		gdosResults: {},
		servicesByLocation: [],
		locationByService: []
	},
	utilities: {
		modal:{},
		expandingHeader: $('.js-doesExpand')
	}
}

symphony.site = {
	common: {
		init: function() {
			//symphony.crossStorage();
			symphony.defaultModal();
			symphony.detectFeatures();
			symphony.donateHeaderActions();
			//symphony.donationsModal();
			symphony.fitVids();
			symphony.geolocate();
			symphony.getHelpActions();
			symphony.handlebarsHelpers();
			//symphony.parseEvents();
			symphony.scrollToNextSection();
			symphony.showOnHover();
			symphony.stickyNav();
			symphony.thumbnailSlider();
			symphony.toggleMenu();

			// Row modules
			symphony.rowDonate();
		}
	},
	home: {
		init: function() {
			symphony.heroSlider();
			
			symphony.videoModal();
		}
	},
	stories: {
		init: function() {
			symphony.getCaringStories();
		}
	},
	our_leader: {
		init: function() {
			symphony.localNeedsScroll();
		}
	}
};

// The routing fires all common scripts, followed by the page specific scripts.
// Add additional events for more control over timing e.g. a finalize event
var UTIL = {
	fire: function(func, funcname, args) {
		var namespace = symphony.site;
		funcname = (funcname === undefined) ? 'init' : funcname;
		if (func !== '' && namespace[func] && typeof namespace[func][funcname] === 'function') {
			namespace[func][funcname](args);
		}
	},
	loadEvents: function() {
		UTIL.fire('common');

		$.each(document.body.className.replace(/-/g, '_').split(/\s+/),function(i,classnm) {
			UTIL.fire(classnm);
		});
	}
};

$(document).ready(UTIL.loadEvents);