var symphony = symphony || {};

;(function() {

	symphony.parseEvents = function() {

		var today = moment();
		var eventListExpiration = today.add(1, 'years');
		var tempArray = [];
		var tempEvent = {};

		symphony.revisedEvents = [];

		_.forEach(symphony.eventsList, function(eventItem, i){

			var frequency = 'days';

			if(eventItem.customFields){
				frequency = eventItem.customFields.frequency;
			}

			if(eventItem.isRecurring !== "false") {
				recurrence = moment().recur({
					start: moment().format(),
					end: eventListExpiration.format(),
					rules: [
						{ units: {  1 : true }, measure: frequency }
					]
				});
				
				tempArray = recurrence.all("LL");

				_(tempArray).forEach(function(value){
					var rebuiltDateTime = value + ' ' + eventItem.startTime;
					//console.log(rebuiltDateTime);
					var tempEvent = {
						"isoStart" : new Date(rebuiltDateTime).toISOString(),
						"startDate" : moment(value).format('LL'),
						"title": eventItem.title,
						"index" : i,
						"customUrl" : eventItem.customUrl,
						"startTime" : eventItem.startTime,
						"isRecurring" : eventItem.isRecurring
					}
					symphony.revisedEvents.push(tempEvent);
				});
			} else {
				var revisedDate = new Date(eventItem.isoStart).toISOString();
				var formattedDate = moment(eventItem.startDate).format('LL');

				eventItem.startDate = formattedDate;
				eventItem.isoStart = revisedDate;
				symphony.revisedEvents.push(eventItem);
			}
			

		});


		var $eventsContainer = $('.eventsList');

		var compiledEventsTemplate = symphony.templates.eventsList;
		var eventsObject = _.orderBy(symphony.revisedEvents, 'isoStart', 'asc');

		$eventsContainer.empty().append(compiledEventsTemplate({ myEvents: eventsObject }));

	};


})();