var symphony = symphony || {};

;(function() {

  symphony.gdosSearchResults = function() {

  		$.getJSON( symphony.config.gdos.search, {

			isoCountry: "us",
			lat:symphony.config.gdos.latLong[0],
			lng:symphony.config.gdos.latLong[1],
			distanceUnits:'MILES',
			limit:100

		}).done(function( data ) {

			symphony.config.gdos.gdosResults = data.objects;

			symphony.gdosGetServicesByLocation();

		});

  };

})();