var symphony = symphony || {};

;(function() {

  symphony.handlebarsHelpers = function() {

		Handlebars.registerHelper('roundedDistance', function (center) {
			return Math.round(center.distance);
		});

		Handlebars.registerHelper('concatAddress', function (center) {
			return center.address1.split(' ').join('+')
		});


  };


})();