var symphony = symphony || {};

;(function() {

  symphony.gdosLatLong = function(getResults) {

  	if(getResults && getResults === true){
		$.getJSON( symphony.config.gdos.postal, {
			isoCountry: "us",
			postalCode: symphony.config.gdos.zip
		}).done(function( data ) {
			var lat = data.latitude;
			var lon = data.longitude;
			symphony.config.gdos.latLong = [lat,lon];

			symphony.gdosSearchResults();
		});
	} else {

	}

  };

})();