var symphony = symphony || {};

;(function() {

  symphony.getHelpActions = function() {

  		var getHelpFirstName = $('#firstName');
  		var getHelpContainer = $('.getHelp-steps');

  	
  		$('.js-stepForward').on('click', function(e){
  			e.preventDefault();
  			getHelpContainer.slick('slickNext');
  		});

  		$('.js-stepBack').on('click', function(e){
  			e.preventDefault();
  			getHelpContainer.slick('slickPrev');
  		});

  		$('.js-getHelp').on('click', function(e){
  			e.preventDefault();
  			if(!getHelpContainer.hasClass('slick-initialized')){
  				getHelpContainer.slick({
  					arrows:false,
  					adaptiveHeight:true,
  					cssEase:'ease-in'
  				});
  			}
  			if(!$('.globalHeader').hasClass('isSticky')){
  				$('.globalHeader').addClass('isSticky');
  			};
        if(symphony.config.utilities.expandingHeader.hasClass('isExpanded')){
          symphony.config.utilities.expandingHeader.removeClass('isExpanded');
        } else {
          $('.getHelp').addClass('isExpanded');
        }
  			
  		});

  		getHelpFirstName.on('keyup keydown', function(){
  			$('.firstName').text($(this).val());
  		});

  };

})();