var symphony = symphony || {};

;(function() {

	symphony.gdosFilterResultsByService = function() {

		var idArr = [];

		$($( ".get-help--services" ).find('input[type="checkbox"]:checked')).each(function(){
			idArr.push($(this).attr('value'));
		});

		symphony.config.gdos.locationByService = [];

		_.forEach(symphony.config.gdos.gdosResults, function (center, i) {

			_(center.services).forEach(function (service) {
				_.find(service.category, function(v){
					if(_.includes(idArr, v) === true){
						symphony.config.gdos.locationByService.push(center);
					}
				});
			});

		});

		symphony.gdosShowCentersByService();

	};

})();