var symphony = symphony || {};

;(function() {

	symphony.stickyNav = function() {

		var $globalHeader = $('.globalHeader');

		var makeOpaque = new Waypoint({
			element: $('.section')[0],
			handler: function(direction) {
				$globalHeader.addClass('isSticky');
			}
		});
		
		function resetNav(){
			var scroll = $(window).scrollTop();
			if(scroll <= 100) {
				if(!symphony.config.utilities.expandingHeader.hasClass('isExpanded')){
		          $globalHeader.removeClass('isSticky');
		        }
			}
		}

		$(window).on('scroll', _.throttle(resetNav, 50));

	};


})();