var symphony = symphony || {};

;(function() {

  symphony.sharePopup = function(network, el) {

			var winTop = (screen.height / 2) - (350 / 2);
			var winLeft = (screen.width / 2) - (520 / 2);
			var shareUrl;
			var url = encodeURIComponent(window.location.href);
			var title = encodeURIComponent($(el).parent().parent().find('.shareCard-stat').text());
			var descr = encodeURIComponent($(el).parent().parent().find('.shareCard-cta').text());
			var image = window.location.origin + ($('.rowStory-photo img').attr('src') || $('.rowStory-photo img').data('src'));

			console.log(title);
			console.log(descr);
			console.log(image);
			console.log(url);

			switch (network) {
				case "Facebook":
					shareUrl = 'http://www.facebook.com/sharer.php?s=100&p[title]=' + title + '&p[summary]=' + descr + '&p[url]=' + url + '&p[images][0]=' + image, 'sharer';
					break;
				case "Pinterest":
					shareUrl = 'https://pinterest.com/pin/create/button/?url=' + url + '&media=' + image + '&description=' + descr;
					break;
				case "Twitter":
					shareUrl = 'https://twitter.com/home?status=' + title + ' - ' + descr + ' - ' + url + ' - via @salvationarmyusa';
					break;
			}
			//console.log(shareUrl, 'sharer', 'top=' + winTop + ',left=' + winLeft + ',toolbar=0,status=0,width=520,height=350');
			window.open(shareUrl, 'sharer', 'top=' + winTop + ',left=' + winLeft + ',toolbar=0,status=0,width=520,height=350');

  };

})();