var symphony = symphony || {};

;(function() {

  symphony.heroSlider = function() {

  	var deviceWidth = $(window).width();
  	var heroText = $('.heroText');
  	var heroContainer = $('.heroContainer');
	var heroHeight = $('.heroContainer').outerHeight();
	var oneThirdHero = heroHeight / 3;
	var twoThirdsHero = oneThirdHero * 2;

	$('.heroSlider').slick({
		//lazyLoad: 'ondemand',
		slide: '.hero',
		fade:true,
		mobileFirst:true,
		swipeToSlide:true,
		dots:true
	});


	// We are loading <img src> into each slide, then using css object-fit to fill the space.
	// Doing this enables us to lazy load the slider images, saving on load times
	// IE does not support this method, so here is a fallback to set the <img src> URl as the slide BG image
	if ( ! Modernizr.objectfit ) {
		$('.hero').each(function () {
			var $container = $(this),
			imgUrl = $container.find('img').data('lazy');
			if (imgUrl) {
				$container
				.css('backgroundImage', 'url(' + imgUrl + ')')
				.addClass('compat-object-fit');
			}  
		});
	}

	if(deviceWidth > 768 && heroContainer.length > 0) {
		function calculateScroll(){
			var scroll = $(window).scrollTop();
			var opacity = 1 - (scroll / (heroHeight / 2));
			var percentageLeft = scroll / heroHeight * 100;
			heroText.css('opacity', opacity).css('margin-top', - percentageLeft);

		}
		var stickyDetect = new Waypoint.Inview({
			element: heroContainer[0],
			enter: function() {
				$(window).on('scroll', _.throttle(calculateScroll, 10));
			},
			exit: function() {
				$(window).on('scroll', _.throttle(calculateScroll, 10));
			},
			exited: function() {
				heroText.css({y:'-50%'});
				$(window).unbind('scroll');
			}
		});
	}

  };

})();