var symphony = symphony || {};

;(function() {

	symphony.getCaringStories = function() {

		//purposefully pulling from a random tag - 'creative', which has 2 posts assigned to it.
		var promise = $.get('//caringsa.com/caringwp/wp-json/wp/v2/posts?filter[tag]=creative');

		promise.done(function(data){
			
			symphony.stories = data;

			// Additional call to get the Featured Image for each post
			$.each(symphony.stories, function(i, story){
				var featuredImage = $.get('//caringsa.com/caringwp/wp-json/wp/v2/media/' + story.featured_media);
				featuredImage.done(function(data){
					symphony.stories[i].media = data;
					$('#stories').empty().append(symphony.templates.featuredStories({story:symphony.stories}));
				});
			});
			
		});

	};


})();