var symphony = symphony || {};

;(function() {

	symphony.gdosShowServicesByLocation = function() {

		var $resultsContainer = $('.results');
		var $nameContainer = $('.name');
		var $name = $('#firstName').val();

		$nameContainer.empty();
		$resultsContainer.empty();

		$nameContainer.html('<p>Hello, ' + $name + '! The following services are available in your location</p>');

		$resultsContainer.wrapAll('<form action="javascript:symphony.gdosFilterResultsByService();" class="get-help--services" />');

		_.forIn(symphony.config.gdos.servicesByLocation, function(value, key){
			$resultsContainer.append('<p><label><input type="checkbox" value="'+ value.id +'"> ' + value.name + '</label></p>');
		});

		$resultsContainer.append('<input type="submit" value="Submit" class="button get-help--submit" />');

	};

})();