var symphony = symphony || {};

;(function() {

	symphony.gdosShowCentersByService = function() {

		var $centersContainer = $('.centers');

		var compiledTemplate = symphony.templates.getHelpResults;
		var filteredCenters = _.sortBy(_.uniqWith(symphony.config.gdos.locationByService, _.isEqual), "distance");

		$centersContainer.empty().append(compiledTemplate({ center: filteredCenters }));

	};

})();